@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:  sans-serif;
}

html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}
.image{
  margin-top: -4%;
  height: 65px;
  margin-left:280px
}
.btn{
  margin-top: 4%;
  border: none;
  border-radius: 4px;
  height: 44px;
  width: 160px;
  background-color: orangered;
  color: white;
  font-size: 17px;
  margin-left: -3%;
}

/* 
 

/* navbar style start  */

.main-nav {
  background-color: white;
margin-top: -10%;
  position: fixed; 
  width: 100%;
  height: 8rem;
  display: grid;
  grid-template-columns: 10rem 1fr 2fr 1fr 10rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.logo {
  display: grid;
  grid-column: 2/3;
  justify-content: start;
  align-items: center;
}
ul li{
  
  list-style: none;
}
.menu-link {
  grid-column: 3/4;
}

.menu-link ul {
  
  list-style: none;
  height: 12rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: -2%;
  
  margin-left: 18%;
  font-size: 18px;
}
.hover:hover {
color: orangered;
}

.social-media {
  grid-column: 4/5;
}

.social-media ul {
  
  list-style: none;
  height: 10rem;
  display: grid;
  grid-template-columns: 3fr repeat(3, 1fr);
  align-items: center;
  justify-content: flex-end;
}

.social-media ul li {
  text-align: right;
  list-style: none;
}

.social-media ul li:first-child {
  grid-column: 2/3;
}


.logo h2 {
  font-size: 2.5rem;
  font-weight: 400;
  text-transform: uppercase;
  background: -webkit-linear-gradient(#eee, rgb(69, 57, 248));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.logo h2 span {
  font-size: 3.5rem;
}

.menu-link ul li {
  font-size: 1.8rem;
}

.menu-link ul li a {
  text-transform: capitalize;
  color: rgba(0, 0, 3, 0.8);
  transition: 0.5s;
}
.menu-link ul li:hover > a {
  transform-origin: left;
  color: rgba(0, 0, 3, 1);
  text-decoration: underline;
  transition: 0.5s;
}

.social-media ul li {
  font-size: 1.8rem;
}

.social-media .hamburger-menu {
  display: none;
}

.facebook {
  color: #3b5998;
}

.instagram {
  color: #c32aa3;
}

.youtube {
  color: #ff0000;
}

/* hero section   */

.hero-section {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.hero-section p {
  font-size: 3rem;
  text-transform: capitalize;
}

.hero-section h1 {
  font-size: 5rem;
  text-transform: uppercase;
  text-align: center;
}

/* responsive css style  */
@media (max-width: 1080px) {
  .main-nav {
    width: 40%;
    position: fixed;
    height: 8rem;
    grid-template-columns: 2rem 3fr 3fr 1fr 2rem;
  }.hover:hover {
    color: orangered;
    }

  .logo,
  .menu-link ul,
  .social-media ul {
    height: 8rem;
  }
  .image{
    margin-top: -4%;
    height: 50px;
    margin-left:10px
  }
  .btn{
    display: none;
    margin-top: 4%;
    border: none;
    border-radius: 4px;
    height: 44px;
    width: 170px;
    background-color: orangered;
    color: white;
    font-size: 17px;
  }
}

/* responsive css style  */
@media (max-width: 998px) {
  .main-nav {
    width: 40%;

    margin-top: 0%;
    position: fixed; 
    height: 7rem;
    grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
  }

  .menu-link {
    display: none;
  }

  .logo,
  .social-media ul {
    height: 7rem;
  }

  .mobile-menu-link {
    grid-column: 2/4;
    position: relative;
    z-index: 99;
  }

  .mobile-menu-link {
    background-color: white;
    height: 20rem;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    padding-left: 3rem;
    transition: all 2s linear;
    transform-origin: top;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  }

  .mobile-menu-link ul {
    height: 20rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: start;
  }

  .mobile-menu-link ul li:first-child {
    transition-delay: 0.2s;
  }

  .social-media {
    grid-row: 1/2;
    grid-column: 3/5;
    justify-items: end;
    align-items: center;
    transition: all 2s linear;
  } 

  .social-media .social-media-desktop {
    height: 0;
    display: none;
  }

   .social-media {
    height: 7rem;
    display: flex;
    justify-self: end;
    align-items: center;
  } 

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 798px) {
  .main-nav {
    width: 40%;
position: fixed;
    height: 6rem;
    grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 520px) {
  .main-nav {

    height: 6rem;
    grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .logo h2 {
    font-size: 2rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }


  .hero-section h1 {
    font-size: 3.8rem;
  }
}